import AUTH from '@/config/web/auth'
import jsonRpc from 'kurento-jsonrpc'
import {
    DEBUG, jsonRpcUrl
} from "@/config/jsonRpc/jsonRpcEnv";

export default {
    DEBUG: false,
    jsonRpcUrl: jsonRpcUrl,
    connected: false,
    jsonRpcClientWs: null,
    sessionId: null,
    userId: null,
    vue: null,
    init(uid, vueObj,debug) {
        this.DEBUG = debug;
        if (!uid) return;
        if (this.connected) return;
        this.vue = vueObj;
        this.userId = uid;
        setTimeout(()=>{
            this.setConnect();
        },500)


    },
    setConnect() {
        let protocol = 'wss';
        if (this.DEBUG) {
            protocol = 'ws';
        }
       const ws_uri = protocol+'://' + this.jsonRpcUrl + '/jsonRpc';

        const configuration = {
            hearbeat: 5000,
            sendCloseMessage: false,
            ws: {
                uri: ws_uri,
                useSockJS: false,
                onconnected: () => {
                    this.connected = true;
                    this.socketInit();
                },
                ondisconnect: this.disconnectCallback(this),
                onreconnected: this.connectReCallback(this),
            },
            rpc: {
                requestTimeout: 5000,
                sendResponse: (params) => {
                    this.callMessage(params)
                },
            }
        };
        this.jsonRpcClientWs = new jsonRpc.clients.JsonRpcClient(configuration);

    },
    close() {
        let params = {};
        params.uid = this.userId;
        params.sessionId = this.sessionId;
        this.jsonRpcClientWs.send("loginClose", params, function (error, response) {
            if (error) {
                console.log(error);
            } else {
                console.log("--succ--");
                console.log(response);
            }
        });
    },
    connectReCallback(obj) {
        obj.connected = true;
    },
    disconnectCallback(obj) {
        obj.connected = false;
        console.log('----------ebd---------');
    },
    callMessage(params) {
        console.log(params);
        switch (params.responseMethod) {
            case 'forceLogOut':
                this.vue.$eventBus.$emit('modalOpen', {
                    message: '다른곳에서 강제 로그아웃 시켰습니다',
                    doAction: () => {
                        AUTH.logout();
                        location.href = '/';
                    }

                });
                break;
        }

    },
    socketInit() {
        let params = {
            interval: 5000,
            operation: "connect",
            uid: this.userId,
        };
        this.jsonRpcClientWs.send('login', params, (error, response) => {
            if (error) {
                console.log(error);
            } else {
                console.log(response, 'succ');
                this.sessionId = response.sessionId;
                switch (response.map.result) {
                    case 'double':
                        this.vue.$eventBus.$emit('modalOpen', {
                            type: 'confirm',
                            message: '다른곳에서 이미 로그인중입니다<br>기존로그인을 강제 종료하시겠습니까',
                            doAction: () => {
                                this.forceLogOut();
                            },
                            cancleAction: () => {
                                AUTH.logout();
                                location.href = '/';
                            }

                        });
                        break;
                    case 'fail':
                        this.vue.$eventBus.$emit('modalOpen', {
                            message: '잘못된 접근입니다',
                            doAction: () => {
                                AUTH.logout();
                                location.href = '/';
                            }

                        });
                        break;
                }

            }

        });

    },
    send(params, func) {
        if (!this.sessionId) return;
        params.uid = this.userId;
        params.sessionId = this.sessionId;
        this.jsonRpcClientWs.send(params.sendMethod, params, function (error, response) {
            if (error) {
                console.log(error);
            } else {
                console.log("--succ--");
                console.log(response);
                func(response);
            }
        });
    },
    forceLogOut() {
        if (!this.sessionId) return;
        let params = {
            uid: this.userId,
            sessionId: this.sessionId,
            responseMethod: 'forceLogOut'
        };
        this.jsonRpcClientWs.send("forceLogOut", params, function (error, response) {
            if (error) {
                console.log(error);
            } else {
                console.log(response);
            }
        });
    },
    post(frm, afterFunc) {

    }


}
