<template>
    <div class="content-all-layout">
        <defaultLayout v-if="layoutShow"></defaultLayout>
        <router-view v-else></router-view>
    </div>
</template>
<script>
    import defaultLayout from './layout/defaultLayout';
    import {mapActions, mapGetters} from "vuex";
    import AUTH from "@/config/web/auth";
    import JQ from 'jquery'
    import {API_URL_GET_SITE_BASE, API_URL_UPDATE_USER_STATISTICS} from "@/config/web/urls";
    import apiService from "@/config/web/apiService";

    import jsonRpcService from "@/common/jsonRpcService";

    export default {
        computed: {
            ...mapGetters('config', ["getListScroll"]),
        },
        metaInfo() {
            return {
                title: this.metaData.title,
                titleTemplate: this.metaData.title, // title 뒤에 붙일 공통된 이름 (주로 사이트이름)
                htmlAttrs: {
                    lang: 'utf-8'
                },
                meta: [
                    // SEO setting
                    {name: 'description', content: this.metaData.description, vmid: 'description'},
                    {name: 'keywords', content: this.metaData.keyword, vmid: 'keywords'},
                    {name: 'author', content: this.metaData.title, vmid: 'author'},
                    // SNS 용
                    /*
                {
                    property: 'og:title',
                    content: this.board.title,
                    template: chunk => `${chunk} | www.ex.com`,
                    vmid: 'og:title'

                },
                */
                ]
            }
        },
        watch: {
            '$route'(to, from) {
                this.checkCurrentUrl(to.name);
            },
            getListScroll(val) {
                this.scrollHandling = val;
            }
        },
        components: {
            defaultLayout,
        },
        data() {
            return {
                DEBUG:((process.env.NODE_ENV =='development')?true:false),
                scrollHandling: false,
                mobile: false,
                layoutShow:true,
                jsonRpc:false,
                metaData: {
                    keyword: '',
                    description: '',
                    title:'',
                },
            }
        },
        created() {
            AUTH.setDevice(this.$isMobile());
            this.checkCurrentUrl(this.$route.name);
            const member = AUTH.getSession();
            if (this.$route.name != 'liveStreamUserLive' &&
                this.$route.name != 'liveStreamHostLive' &&
                this.$route.name != 'liveStreamHostLivePc'  &&
                this.$route.name != 'liveStreamHostLiveMobile'  &&
                this.$route.name != 'liveStreamUserLivePc'  &&
                this.$route.name != 'liveStreamUserLiveMobile'  &&
                this.$route.name != 'testJsonRpc'  &&
                this.$route.name != 'testUserJsonRpc') {
                if (member && member?.memberInfo && !this.DEBUG) {

                    this.jsonRpc = true
                    jsonRpcService.init(member.memberInfo.uid,this,this.DEBUG);
                }
            }
        },
        methods: {
            ...mapActions('config', ["setListScrollMore"]),
            checkCurrentUrl(name) {
                if(this.$route.name == 'testJsonRpc') this.layoutShow = false
                else if(this.$route.name == 'testUserJsonRpc') this.layoutShow = false
                else if (name.indexOf('videoSetting') !== -1) this.layoutShow = false
                else {
                    if (name.indexOf('liveStream') !== -1) this.layoutShow = false
                    else this.layoutShow = true
                }
            },
            handleListScroll(e) {

                if(this.scrollHandling) {
                    let checkHeight = (this.$isMobile()) ? 400 : 700;
                    let moveHeight = JQ('html,body').prop('clientHeight') + JQ('html,body').scrollTop() + checkHeight;
                    let screenHeight = JQ('html,body').prop('scrollHeight');
                    if (screenHeight < moveHeight) {
                        this.setListScrollMore(true);
                    }
                }

            },
            handleBeforeUnload(event) {
                return false;
            },
        },
        mounted() {
            if(this.jsonRpc) {
                JQ(window).on("beforeunload", function (eventObject) {

                    jsonRpcService.close();
                    return false;
                });
            }
        },
        beforeDestroy() {

        },

    }
</script>
<style lang="scss" scoped>
    .content-all-layout {

        width: calc(100vw);
        max-width: calc(100vw);
    }


</style>
<style lang="scss">

    /*
     font-family: 'Proxima Nova';
     */
    .blackButton {
        background: #000 !important;
        color: #fff !important;
        height: 57px !important;
        width: 100%;

        span {
            font-size: 16px;
            font-weight: bold !important;
        }

    }

    .whiteButton {
        background: #fff !important;
        color: #000 !important;
        border: 1px solid #CDCDCD;
        height: 57px !important;
        width: 100%;

        span {
            font-size: 16px;
            font-weight: bold !important;
        }

    }

    .string-cut {
        display: inline-block;
        white-space: nowrap;
        overflow: hidden;

        text-overflow: ellipsis; /* 여러 줄 자르기 추가 스타일 */
        white-space: normal;
        word-wrap: break-word;
        display: -webkit-box;
        text-align: left;
        -webkit-box-orient: vertical;

    }

    @media screen and (min-width: 1260px) {
        .tablet-mobile-element {
            display: none !important;
        }
        .mobile-element {
            display: none !important;
        }
        .tablet-element {
            display: none !important;
        }

    }

    @media screen and (min-width: 450px) and (max-width: 1259px) {
        .pc-mobile-element {
            display: none !important;
        }
        .mobile-element {
            display: none !important;
        }
        .pc-element {
            display: none !important;
        }

    }

    @media screen and (max-width: 450px) {
        .tablet-element {
            display: none !important;
        }
        .pc-tablet-element {
            display: none !important;
        }

        .pc-element {
            display: none !important;
        }
    }

</style>
