<template>
    <div :class="'default-page-container '+mobileClass">
        <div class="header">

            <div :class="'header-container '+topClass" v-if="mobile">
                <div class="logo" @click="goPage('/')">
                    <img src="../../../assets/images/logo_w.png">

                </div>
                <div class="menu-name" @click="goMypage()">
                    <font-awesome-icon style="font-size:16pt;color:#fff;" icon="fa-solid fa-user"/>
                </div>

            </div>
            <div :class="'header-container '+topClass" v-else>
                <div class="logo" @click="goPage('/')">
                    <img src="../../../assets/images/logo_w.png" v-if="topClass=='mainTop'">
                    <img src="../../../assets/images/logo.png" v-else>

                </div>
                <div class="menu-pannel">
                    <ul class="menu">
                        <li class="menu-item">
                            <div class="menu-name" @click="goDemo()">데모체험</div>
                        </li>
                        <li class="menu-item">
                            <div class="menu-name" @click="goMypage()">
                                <font-awesome-icon style="font-size:20pt" icon="fa-solid fa-user"/>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </div>

        <div class="content-body">
            <div class="content">
                <router-view/>
            </div>
        </div>
        <div class="bottom" v-if="!mobile">
            <div class="bottom-container">
                <div class="bottom-logo" @click="goPage('/')">
                </div>
                <div class="bottom-copy">
                </div>
            </div>
        </div>
    </div>
</template>
<script>

    import AUTH from '@/config/web/auth'
    import JQ from "jquery";

    export default {
        name: 'defaultLayout',
        watch: {
            '$route'(to, from) {
                this.checkCurrentUrl(to.name);
            },

        },
        data() {
            return {
                isLogin: false,
                menuPannelShow: false,
                topClass: 'mainTop',
                mobile: this.$isMobile(),
                mobileClass: '',
            };
        },
        created() {
            this.mobileClass = (this.$isMobile()) ? 'mobile' : '';

            const member = AUTH.getSession();
            if (member && member?.memberInfo) {
                this.isLogin = true;
            } else {
                //this.$router.push('/memberLogin')
                //return;
            }
            this.checkCurrentUrl(this.$route.name);
            //this.handleResize();
        },
        methods: {
            checkCurrentUrl(name) {
                if (name == 'siteMain') this.topClass = 'mainTop'
                else this.topClass = 'default'


            },
            menuHide() {

            },
            goPage(url) {
                this.$router.push(url)
            },
            goDemo() {
                if (this.isLogin) {
                    this.$router.push('/video/step')
                } else {
                    this.$router.push('/memberLogin')
                }
            },
            goMypage() {
                if (this.isLogin) {
                    this.$router.push('/mypage/main')
                } else {
                    this.$router.push('/memberLogin')
                }
            }
        },
        mounted() {

            //window.addEventListener('resize', this.handleResize);
        },
        updated() {

        },
        beforeDestroy() {
            //window.removeEventListener('resize', this.handleResize);
        },

    }
</script>

<style lang="scss" scoped>
    .default-page-container {
        height: auto;

        .header {
            width: 100%;
            background: none;
            height: 75px;
            position: fixed;
            top: 0;
            left: 0;
            z-index: 10;

            .header-container {
                position: relative;

                .logo {
                    position: absolute;
                    top: 20px;
                    left: 30px;
                    cursor: pointer;
                }

                .menu-pannel {
                    width: 100%;
                    height: 75px;

                    .menu {
                        width: 100%;
                        display: flex;
                        justify-content: right;
                        padding: 0;

                        .menu-item {
                            margin: 0 20px;
                            padding-top: 25px;

                            .menu-name {
                                font-size: 18px;
                                margin-right: 20px;
                                cursor: pointer;
                                color: #fff;

                                &.active {
                                    font-weight: bold;
                                }

                            }
                        }

                    }
                }

                &.default {
                    background: #fff;
                    border-bottom: solid 1px #dfdfdf;

                    .menu-pannel {
                        .menu {
                            .menu-item {
                                .menu-name {
                                    color: #000;
                                }
                            }

                        }
                    }
                }
            }

        }

        .content-body {
            width: 100%;
            background: #fff;
            padding: 100px 0;

            .content-container {
                width: 1200px;

                .content {
                    width: 100%;
                }
            }
        }

        .bottom {
            width: 100%;
            height: 90px;
            padding: 0;
            background: #fff;

            .bottom-container {
                width: 1200px;
                margin: auto;
                padding: 0 0 50px 0;
                display: flex;
                justify-content: space-between;
                padding: 40px 0 20px 0;

                .bottom-logo {
                    img {
                        cursor: pointer;
                        margin-top: -13px;
                        width: 170px;
                    }
                }

                .bottom-copy {
                    font-size: 12px;
                    color: #999999;
                    padding: 0;
                }

                .bottom-menu {
                    min-width: 500px;
                    padding: 0;
                    margin: 0 10px 0 0;
                    display: flex;
                    justify-content: right;

                    li {
                        height: 10px;
                        cursor: pointer;
                        color: #999999;
                        padding: 0;
                        font-size: 12px;
                        text-align: center;

                        &.dot {
                            cursor: default;
                            width: 1px;
                            margin: 4px 20px 0 20px;
                            border-right: solid 1px #999;
                        }
                    }
                }

            }
        }

        &.mobile {
            .header {
                width: 100%;
                height:55px;
                position: fixed;
                top: 0;
                left: 0;
                z-index: 100;
                background: #1e90ff;
                .header-container {
                    position: relative;

                    .logo {
                        position: absolute;
                        top: 20px;
                        left: 30px;
                        cursor: pointer;
                        img {
                            width:100px;
                        }

                    }
                    .menu-name {
                        position: absolute;
                        top: 20px;
                        right: 30px;
                        cursor: pointer;
                        color: #000;
                    }

                    .menu-pannel {
                        display: none;
                    }
                }
            }

            .content-body {
                padding: 0;

                .content-container {
                    width: 100%;

                    .content {
                        width: 100%;
                    }
                }
            }
        }
    }


</style>
